import Layout from "../../layout/simpleLeftTop";
import { Form, Input, Button, Checkbox, message } from 'antd';
import { apiChangePwd } from '../../utils/apis'

const ChangePwd = () => {
  const onFinish = (values: any) => {
    console.log('Success:', values);
    const obj = {
      ...values
    }
    apiChangePwd(obj).then(res => {
      if(res.code === 0) {
        message.success('')
        setTimeout(() => {
          window.localStorage.removeItem('token')
          window.location.href = '/manage/login'
        }, 2000)
      } else {
        message.error('' + res.msg)
      }
    })
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Layout>
      <h2></h2>
      <Form
        name="basic"
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 8 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item
          label=""
          name="oldname"
          rules={[{ required: true, max:128, message: '!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label=""
          name="oldpwd"
          rules={[{ required: true, max:128, message: '!' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label=""
          name="newname"
          rules={[{ required: true, max:128, message: '!' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="newpwd"
          rules={[{ required: true, max:128, message: '!' }]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 4, span: 16 }}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </Layout>
  )
}
export default ChangePwd